.vc_row {
  .--row-has-overflow {
    overflow: hidden;
  }
  .custom-heading {
      .contact-2-heading {
        h2 {
          position: relative;
          display: inline-block;
          margin-bottom: 30px !important;
          &:after {
            content: '';
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 3px;
            background: var(--primary-color);
          }
        }
        p {
          font-size: 16px;
          color: #D1D1D1;
          font-weight: 600;
          br {
            line-height: 32px;
          }
        }
    }
  }
  .overflow-form {
    #wpcf7-f886-p887-o1 {
      overflow: hidden;
    }
    >.vc_column-inner {
      border-top-right-radius: inherit !important;
      border-bottom-right-radius: inherit !important;
      box-shadow: 0 10px 21px 0 rgba(0, 0, 0, .06);
    }


    }
  .left-image {
    position: absolute;
    left: -370px;
    top: -245px;
    width: 722px;
    height: 815px;
    z-index: -1;
    @include breakpoint(small only) {
      display: none;
    }
  }
  .right2-image {
    position: absolute;
    right: -340px;
    width: 240px;
    height: 240px;
    top: -100px;
    @include breakpoint(small only) {
      display: none;
    }
  }
  .right1-image {
    position: absolute;
    right: -90px;
    width: 240px;
    height: 240px;
    bottom: -173px;
    z-index: -1;
    @include breakpoint(small only) {
      display: none;
    }
  }
  &.custom-map {
    top: -200px;
    margin-bottom: -462px;
    z-index: -1;
    @include breakpoint(small only) {
      top: 0;
      margin-bottom: 0px;
    }
    .wpb_column {
      .vc_column-inner {
        padding: 0;
        @include breakpoint(small only) {
          padding: 15px;
        }
      }
    }
  }

  .contact-informations {
    >.vc_column-inner {
      border-top-left-radius: inherit !important;
      border-bottom-left-radius: inherit !important;
      box-shadow: 0 10px 21px 0 rgba(0, 0, 0, .06);
      @include breakpoint(small only) {
        padding:  50px 15px !important;
      }
    }
    .boxes.layout-1 {
      .box-icon {
        .fa {
          color: #001774;
          font-size: 25px;
          &.fa-mobile {
            font-size: 35px;
          }
          &.fa-map-marker {
            font-size: 30px;
          }
        }
      }

      .box-title-1 {
        color: #333333;
        text-transform: uppercase;
        font-weight: 700;
        font-size: 16px;
      }

      .box-body {
        color: #FFFFFF;
        font-weight: 600;
        font-size: 13px;
        br {
          line-height: 24px;
        }
      }
    }
  }
  }


.top-header {
  background-color: #131313;
}

.call-button{
  .wd-btn{
    float: left;
    margin-right: 19px;
  }
  p{
    font-weight: 700;
    color: #000;
    padding-top: 10px;
  }
}


body {
  .wd-heading {
    .title_a {
      font-size: 36px;
      font-weight: 800;
      @include breakpoint(large up) {
        font-size: 60px;
        margin-bottom: 10px;
        text-transform: uppercase;
      }
      position: relative;
      z-index: 2;
      &:after {
        @include breakpoint(large up) {
          content: attr(data-text);
          display: block;
          height: 100%;
          position: absolute;
          left: -20px;
          right: 0;
          top: -58px;
          /* transform: scale(2.5); */
          transform-origin: left;
          opacity: .05;
          z-index: -1;
          font-size: 120px;
      }
    }
  }
  .sub_title_a {
    display: none;
  }

  .title_b {
    margin-top: 3px;
  }
  .sub_title_b {
    margin: 0;
  }
}
.dashed-bg {
  &:before {
    background-image: url(../images/pattern-brick.png);
    background-color: black;
    content: "";
    display: block;
    height: 100%;
    width: 100%;
    position: absolute;
    left: 0;
  }
}
}

.best-services .text-icon__text {
  font-size: 15px;
  line-height: 1.6;
  margin-top: -6px;
}


.specialization {
  .text-icon {
    padding: 40px 40px 30px;
    .text-icon__icon-box {
      img {
        box-shadow: 0px 0px 0px 0px var(--primary-color);
        transition: box-shadow .25s ease-in-out;
        padding: 8px 20px;
        will-change: box-shadow;
        min-height: 80px;
      }
    }
    &:hover {
      .text-icon__icon-box {
        img {
          box-shadow: 0px 0px 0px 3px var(--primary-color);
        }
      }
    }
  }
}